html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  overflow: hidden;
}

.page {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 1rem 2.5rem;
  overflow-y: auto;
}

.page.light {
  color: #212529;
  background: #f8f9fa;
}

.page.dark {
  color: #f8f9fa;
  background: #212529;
}

.nav {
  padding: .5rem .4rem;
}

a {
  color: #0d6efd;
  transition: color .15s ease-in-out;
}

a:hover {
  color: #0a58ca;
}

.page.dark a {
  color: #6ea8fe;
}

.page.dark a:hover {
  color: #9ec5fe;
}

button {
  text-align: center;
  vertical-align: middle;
  user-select: none;
  cursor: pointer;
  color: #fff;
  background-color: #0d6efd;
  border: 1px solid #0d6efd;
  border-radius: .25rem;
  padding: .375rem .75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  transition: all .15s ease-in-out;
  display: inline-block;
}

button:hover {
  background-color: #0b5ed7;
  border-color: #0a58ca;
}

button:active {
  background-color: #0a58ca;
  border-color: #0a53be;
}

button.like {
  background-color: #6c757d;
  border-color: #6c757d;
  margin: 1rem .25rem;
  display: block;
}

button.like:hover {
  background-color: #5c636a;
  border-color: #565e64;
}

#theme-menu {
  padding-bottom: 1.5rem;
}

#theme-menu button {
  margin: 0 .25rem;
}

.content {
  margin: 1.5rem 0;
  font-size: 1rem;
  line-height: 1.6;
}

.content i {
  color: #6c757d;
  font-style: italic;
}

.page.dark .content i {
  color: #adb5bd;
}

.counter {
  vertical-align: top;
  text-align: center;
  background: #fff;
  border: 1px solid #00000020;
  border-radius: .375rem;
  min-width: 200px;
  margin-bottom: 1.5rem;
  margin-right: 1.5rem;
  padding: 1.25rem;
  display: inline-block;
  box-shadow: 0 .125rem .25rem #00000013;
}

.counter button {
  min-width: 100px;
  margin: .5rem .25rem;
}

.page.dark .counter {
  color: #f8f9fa;
  background: #343a40;
  border-color: #495057;
}

.notification {
  color: #0c5460;
  background-color: #d1ecf1;
  border: 1px solid #bee5eb;
  border-radius: .375rem;
  margin-top: 1rem;
  padding: .75rem 1.25rem;
  font-size: 1rem;
  line-height: 1.5;
  display: none;
  position: relative;
}

.notification.show {
  display: block;
}

.notification button {
  color: #fff;
  background-color: #0c5460;
  border-color: #0c5460;
  min-width: 80px;
  margin: .75rem auto 0;
  display: block;
}

.notification button:hover {
  background-color: #0a4349;
  border-color: #093b3f;
}

.page.dark .notification {
  color: #c3e6f1;
  background-color: #1b4955;
  border-color: #155360;
}

.page.dark .notification button {
  background-color: #17a2b8;
  border-color: #17a2b8;
}

.page.dark .notification button:hover {
  background-color: #138496;
  border-color: #117a8b;
}

#state {
  color: #212529;
  background: #fff;
  border: 1px solid #dee2e6;
  border-bottom-left-radius: .375rem;
  min-width: 280px;
  margin: 0;
  padding: 1rem;
  font-family: Courier New, monospace;
  font-size: .75rem;
  position: fixed;
  top: 0;
  right: 0;
  box-shadow: 0 .5rem 1rem #00000026;
}

.page.dark #state, .page.dark #note {
  color: #f8f9fa;
  background: #343a40;
  border-color: #495057;
}

.dates-picker {
  max-width: 300px;
  margin: 1.5rem 0;
}

.dates-picker .ui-row {
  align-items: center;
  gap: .5rem;
  margin-bottom: .8rem;
  display: flex;
}

.dates-picker input {
  box-sizing: border-box;
  border: 1px solid #ced4da;
  border-radius: .25rem;
  width: 100%;
  padding: .5rem .75rem;
  font-size: 1rem;
}

.dates-picker input:focus {
  border-color: #0d6efd;
  outline: none;
  box-shadow: 0 0 0 .2rem #0d6efd40;
}

.page.dark .dates-picker input {
  color: #f8f9fa;
  background: #343a40;
  border-color: #495057;
}

.page.dark .dates-picker input:focus {
  border-color: #6ea8fe;
  box-shadow: 0 0 0 .2rem #6ea8fe40;
}

.dates-picker .help-toggle {
  padding: 2px 6px 0;
}

.dates-picker .dates-info {
  color: #6c757d;
  font-size: .85rem;
}

.page.dark .dates-picker .dates-info {
  color: #adb5bd;
}

.dates-picker .dates-list {
  background: #fff;
  border: 1px solid #ced4da;
  border-radius: .25rem;
  max-height: 200px;
  margin: 0;
  padding: 0;
  list-style: none;
  overflow-y: auto;
}

.page.dark .dates-picker .dates-list {
  background: #343a40;
  border-color: #495057;
}

.dates-picker .dates-list li {
  cursor: pointer;
  border-bottom: 1px solid #e9ecef;
  padding: .5rem .75rem;
  transition: background-color .1s;
}

.dates-picker .dates-list li:last-child {
  border-bottom: none;
}

.dates-picker .dates-list li:hover {
  background: #e9ecef;
}

.dates-picker .dates-list li.selected {
  color: #fff;
  background: #0d6efd;
}

.dates-picker .dates-list li.selected:hover {
  background: #0b5ed7;
}

.page.dark .dates-picker .dates-list li {
  border-bottom-color: #495057;
}

.page.dark .dates-picker .dates-list li:hover {
  background: #495057;
}

.page.dark .dates-picker .dates-list li.selected {
  color: #212529;
  background: #6ea8fe;
}

.page.dark .dates-picker .dates-list li.selected:hover {
  background: #9ec5fe;
}
